import React, { useState, Component } from 'react'
import { Breadcrumb, Layout, Menu } from 'antd';
import CamItem from '../CamItem';

const { Header, Content, Footer, Sider } = Layout;

function WebStyle(props) {
    const [imgState, setimgState] = useState(true);
    const [imgData, setimgData] = useState('');
    const _upImg = () => {
        let upimgs = document.getElementById("upimg")
        upimgs.click();
    }
    const upChange = (event) => {
        let imgfile = event.currentTarget.files[0];///input
        console.log('', imgfile)
        if (imgfile.size > 1024000) {
            alert('1m', 3)
            let upimgs = document.getElementById("upimg")
            upimgs.value = ''
        }
        else {
            //readAsDataURL
            let reader = new FileReader();//filereader.readasdataurl
            reader.readAsDataURL(imgfile);

            reader.onload = function (event) {//load
                let imgs = this.result
                console.log('Base64', imgs)
                setimgState(false)
                setimgData(imgs)
            };
        }
    }
    return (
        <div className='page-body'>
            <button onClick={_upImg}>
                {
                    imgState ? '' : ''
                }
            </button>
            <div>
                <img src={imgData} style={{ width: '25%', height: '30%' }} />
            </div>
            <input id='upimg' type='file' style={{ display: 'none' }} accept='image/jpg, image/png' onChange={upChange} />
        </div>
    )
}

class ReIDAGW extends Component {
    constructor(props) {
        super(props)
        this.state = {
            imgState: true,
            imgData: ''
        }
    }
    _upImg = () => {
        let upimgs = document.getElementById("upimg")
        upimgs.click();
    }
    upChange = (event) => {
        let _this = this//this
        let imgfile = event.currentTarget.files[0];///input
        console.log('', imgfile)
        if (imgfile.size > 1024000) {
            alert('1m', 3)
            let upimgs = document.getElementById("upimg")
            upimgs.value = ''
        }
        else {
            //readAsDataURL
            let reader = new FileReader();//filereader.readasdataurl
            reader.readAsDataURL(imgfile);

            reader.onload = function (event) {//load
                let imgs = this.result
                console.log('Base64', imgs)
                _this.state.imgState = false
                _this.state.imgData = imgs
                _this.setState(_this.state)
            };
        }
    }
    render() {
        const data = [{ id: 1, camId: "0", pic: NaN }, { id: 2, camId: "1", pic: NaN }, { id: 3, camId: "2", pic: NaN }, { id: 4, camId: "3", pic: NaN }, { id: 5, camId: "4", pic: NaN }, { id: 6, camId: "5", pic: NaN }]
        return (
            <Layout>
                <Sider className="site-layout-background" width={200}>
                    <div className='page-body'>
                        <button type="primary" style={{ margin: "5px 5px 5px 5px" }} className="bar__btn" onClick={this._upImg}>
                            {
                                this.state.imgState ? '' : ''
                            }
                        </button>
                        <button type="primary" style={{ margin: "5px 5px 5px 5px" }} className="bar__btn" onClick={this.predict}>
                        </button>
                        <div>
                            <img src={this.state.imgData} style={{ width: '100%', height: '100%' }} />
                        </div>
                        <input id='upimg' type='file' style={{ display: 'none' }} accept='image/jpg, image/png' onChange={this.upChange} />
                    </div>
                </Sider>
                <Content>
                    {
                        data.map(item => {
                            return <CamItem key={item.id} data={item} />
                        })
                    }
                </Content>
            </Layout>
        )
    }
    predict = () => {
        const url = "http://127.0.0.1:5000/AGW_predict"
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                imgbase64: 'this.state.imgData'
            })
        }).then(response => response.json()).then(data => { console.log(data) }).catch(error => { console.error(error) })
    }

}

export default ReIDAGW
